import React from 'react';
import styled from 'styled-components';
import { Container } from '../common/Container/Container';
import { device } from '../../utils/layoutBreakpoints';
import { colors } from '../../utils/colors';
import { useInView } from 'react-cool-inview';
import { GetDemoForm } from '../common/GetDemoForm';
import { Text } from '../common';

const StyledGetDemoContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 50px 0;
  text-align: center;
  justify-content: center;
  @media ${device.laptop} {
    padding: 50px;
  }

  span {
    color: rgb(116, 117, 119);

    @media ${device.tablet} {
      margin: 0 auto;
      width: 70%;
    }
  }
`;

const GetTrialSection = () => {
  const { observe, inView } = useInView({});
  return (
    <Container>
      <StyledGetDemoContainer ref={observe}>
        <Text.Title.Bold.lg>Test Drive Telgorithm</Text.Title.Bold.lg>
        <Text.Regular.Normal.md color={colors.secondary.gray300}>
          Interested in testing out Telgorithm’s SMS API technology for 10DLC messaging?
          As a Campaign Service Provider (CSP), you can sign up for a 30-day risk free
          pay-as-you-go (PAYG) trial today.
        </Text.Regular.Normal.md>
        <div>
          <GetDemoForm formId="8377e352-485e-4187-acd5-c5be35d99991"></GetDemoForm>
        </div>
      </StyledGetDemoContainer>
    </Container>
  );
};

export default GetTrialSection;
