import React, { FC, ReactElement } from 'react';
import {
  QuoteIconsContainer,
  StyledTestimonialCard,
  TestimonialCardContent,
  TestimonialCardLogoContainer,
} from './styled';
import SingleQuoteIcon from '../Icons/SingleQuoteIcon';
import { Text } from '../Text';

type Props = {
  companyLogo: ReactElement;
  text: string;
};

const TestimonialCard: FC<Props> = ({ companyLogo, text }) => {
  return (
    <StyledTestimonialCard>
      <TestimonialCardLogoContainer>
        {companyLogo}
        <QuoteIconsContainer>
          <SingleQuoteIcon />
          <SingleQuoteIcon />
        </QuoteIconsContainer>
      </TestimonialCardLogoContainer>
      <TestimonialCardContent>
        <Text.Regular.Normal.md>{text}</Text.Regular.Normal.md>
      </TestimonialCardContent>
    </StyledTestimonialCard>
  );
};

export default TestimonialCard;
