import React from 'react';
import styled from 'styled-components';
import { Container } from '../common/Container/Container';
import { Text } from '../common';
import { device } from '../../utils/layoutBreakpoints';
import { SectionTitleText } from '../common/Text';
import LayersIcon from '../common/Icons/Home/LayersIcon';
import FileIcon from '../common/Icons/Home/FileIcon';
import StarIcon from '../common/Icons/Home/StarIcon';
import { Link } from 'gatsby';
import background from '../../images/Home/splash.png';
import { DecisionBlock } from './DecisionBlock';
import { colors } from '../../utils/colors';

const MainInfo = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  margin-bottom: 40px;

  @media ${device.laptop} {
    text-align: center;
    ${SectionTitleText} {
      width: 75%;
    }
    margin: 0px auto 80px;
    width: 85%;
  }
`;

const AdvantageItem = styled(Link)`
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 50px 50px;
  align-items: center;
  justify-items: center;
  & > div:first-child {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

const Advantages = styled.div`
  display: grid;
  gap: 65px;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;

  @media ${device.tablet} {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    margin-bottom: 80px;
  }

  @media ${device.laptop} {
    gap: 40px;
    margin-bottom: 0;
  }
`;

const StyledDecisionSection = styled.section`
  background-color: #282a3b;
  margin-bottom: 50px;
  margin-top: 50px;
  padding: 25px 0;

  @media ${device.laptop} {
    padding: 75px;
  }
`;

const AdvantageTitle = styled.p`
  font-weight: 700;
  font-size: 0.88rem;
  line-height: 1.3rem;
  height: 20px;
`;

const AdvantageText = styled.p`
  font-weight: 400;
  font-size: 0.8rem;
  line-height: 1.1rem;
  width: 100%;
  align-self: flex-start;
  color: #767676;

  @media ${device.mobileL} {
    width: 80%;
  }
`;

const Content = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 50px;
  z-index: 1;
  position: relative;
  padding: 50px 0;

  h2 {
    color: ${colors.primary.white};
  }

  @media ${device.laptop} {
    padding: 50px;
  }
  &::before {
    background-image: url(${background});
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.3;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
`;

const DecisionBlocks = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 50px;

  @media ${device.laptop} {
    flex-direction: row;
  }
`;

const DecisionSection = () => {
  return (
    <StyledDecisionSection>
      <Container>
        <Content>
          <Text.Title.Bold.lg>Is Telgorithm for you?</Text.Title.Bold.lg>
          <DecisionBlocks>
            <DecisionBlock
              isNegative
              organizationActivity="business wanting"
              organizationOffer="to send 10DLC messages to your customers"
              points={[
                'Telgorithm is just the APIs - we don’t have an application or platform to send messages from.',
                'You’re likely looking for a software provider. Let us know if you want a referral to one of our awesome customers!',
              ]}
            />
            <DecisionBlock
              organizationActivity=" software Provider"
              organizationOffer="offering 10DLC messaging services to your customers"
              points={[
                'Telgorithm’s APIs are like an outlet you can plug your software into.',
                'Telgorithm can power your text messaging offering with our 10DLC-first APIs & unique tech features.',
              ]}
            />
          </DecisionBlocks>
        </Content>
      </Container>
    </StyledDecisionSection>
  );
};

export default DecisionSection;
