import * as React from 'react';
import SuccessIcon from '../../common/Icons/SuccessIcon/SuccessIcon';
import CrossIcon from '../../common/Icons/CrossIcon/CrossIcon';
import SuccessEmptyIcon from '../../common/Icons/SuccessEmptyIcon';
import {
  DecisionBlockContent,
  DecisionBlockHeader,
  DecisionIconContainer,
  DecisionPoint,
  DecisionPointMarker,
  DecisionPoints,
  StyledDecisionBlock,
} from './styled';
import { Text } from '../../common';
import RightPlaneArrow from '../../common/Icons/Arrows/RightPlaneArrow';
import { colors } from '../../../utils/colors';

type Props = {
  isNegative?: boolean;
  organizationActivity: string;
  organizationOffer: string;
  points: string[];
};

export const DecisionBlock = ({
  isNegative = false,
  organizationActivity,
  points,
  organizationOffer,
}: Props) => {
  return (
    <StyledDecisionBlock isNegative={isNegative}>
      <DecisionBlockHeader>
        <Text.Regular.Normal.md>You are a</Text.Regular.Normal.md>
        <Text.Regular.Bold.xl>{organizationActivity}</Text.Regular.Bold.xl>
        <Text.Regular.Normal.md>{organizationOffer}</Text.Regular.Normal.md>
      </DecisionBlockHeader>
      <DecisionBlockContent>
        <DecisionPoints>
          {points.map((point) => (
            <DecisionPoint key={point}>
              <DecisionPointMarker>
                <RightPlaneArrow
                  gradientStopColors={isNegative ? ['#484B65', '#484B65'] : undefined}
                />
              </DecisionPointMarker>
              <Text.Regular.Normal.md>{point}</Text.Regular.Normal.md>
            </DecisionPoint>
          ))}
        </DecisionPoints>
      </DecisionBlockContent>
      <DecisionIconContainer>
        {isNegative ? (
          <CrossIcon color={colors.primary.white} />
        ) : (
          <SuccessEmptyIcon color={colors.primary.white} />
        )}
      </DecisionIconContainer>
    </StyledDecisionBlock>
  );
};
