import styled from 'styled-components';
import { colors } from '../../../utils/colors';
import { device } from '../../../utils/layoutBreakpoints';

export const StyledTestimonialCard = styled.div`
  background-color: ${colors.primary.white};
  border-radius: 16px;
  padding: 25px;

  display: flex;
  flex-direction: column;
  gap: 15px;

  @media ${device.laptop} {
    padding: 25px;
    border-radius: 20px;
  }
`;

export const TestimonialCardLogoContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;

    svg {
        width: 18px;
        height: 24px;
        path {
            fill: ${colors.secondary.gray200};
        }
    }
`

export const TestimonialCardContent = styled.div`
    span {
        color: #767676;
    }
`

export const QuoteIconsContainer = styled.div`
    display: flex;
`
